import { useCallback, useEffect, useMemo, useState } from 'react';
import { useRouter } from './use-router';

interface Options {
  itemsPerPage?: number;
  initialPage?: number;
  withRouting?: boolean;
}

export const usePaginatedData = <T>(
  data: T[],
  { itemsPerPage = 10, initialPage = 1, withRouting = false }: Options = {},
) => {
  const [paginatedData, setPaginatedData] = useState<T[]>([]);
  const [currPage, setCurrPage] = useState(initialPage);
  const { navigate } = useRouter();

  useEffect(() => {
    console.log(1);
    if (withRouting) {
      console.log(2);
      setCurrPage(initialPage);
    }
  }, [withRouting, initialPage]);

  useEffect(() => {
    console.log(3);
    const startIndex = (currPage - 1) * itemsPerPage;
    const endIndex = currPage * itemsPerPage;
    setPaginatedData(data.slice(startIndex, endIndex));
  }, [data, currPage, itemsPerPage]);

  const lastPage = useMemo(
    () => Math.ceil(data.length / itemsPerPage),
    [data.length, itemsPerPage],
  );

  const setPage = useCallback((page: number, callback?: (page?: number) => void) => {
    if (page > lastPage) {
      page = lastPage;
    }
    if (page < 1) {
      page = 1;
    }

    if (withRouting) {
      navigate(`?page=${page}`);
      callback?.(page);
    }

    setCurrPage(page);
  }, []);

  return { paginatedData, currPage, setPage, lastPage };
};
